export const koKR = {
  components: {
    MuiBreadcrumbs: {
      defaultProps: {
        expandText: ' '
      }
    },
    MuiTablePagination: {
      defaultProps: {
        getItemAriaLabel: type => {
          if (type === 'first') {
            return '   ';
          }
          if (type === 'last') {
            return '  ';
          }
          if (type === 'next') {
            return '  ';
          }
          // if (type === 'previous') {
          return '  ';
        },
        labelRowsPerPage: '  :',
        labelDisplayedRows: ({
          from,
          to,
          count
        }) => `${from}${to} / ${count !== -1 ? count : `${to} `}`
      }
    },
    MuiRating: {
      defaultProps: {
        getLabelText: value => `${value} `,
        emptyLabelText: ' '
      }
    },
    MuiAutocomplete: {
      defaultProps: {
        clearText: '',
        closeText: '',
        loadingText: ' ',
        noOptionsText: ' ',
        openText: ''
      }
    },
    MuiAlert: {
      defaultProps: {
        closeText: ''
      }
    },
    MuiPagination: {
      defaultProps: {
        'aria-label': ' ',
        getItemAriaLabel: (type, page, selected) => {
          if (type === 'page') {
            return `${page}  ${selected ? '' : ' '}`;
          }
          if (type === 'first') {
            return '   ';
          }
          if (type === 'last') {
            return '  ';
          }
          if (type === 'next') {
            return '  ';
          }
          // if (type === 'previous') {
          return '  ';
        }
      }
    }
  }
};